import { useState, CSSProperties, useCallback,useRef } from "react";
import Footer from "../../components/Footer/Footer";
import Navbar from "../../components/navbar/Navbar";
import "./checkout.css";
import { Link } from "react-router-dom";
import { publicRequest } from "../../requestMethods";
import ClipLoader from "react-spinners/ClipLoader";
import { useForm } from "react-hook-form";

const Checkout = () => {
  const [inputs, setInputs] = useState({});
  const topRef = useRef(null);
  const order = JSON.parse(localStorage.getItem("order"));
  const date = localStorage.getItem("arrivaldate");
  const [openBankTransfer, setOpenBankTransfer] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [pesapal, setPesapal] = useState(false);
  const [mpesa, setMpesa] = useState(false);
  const [status, setStatus] = useState("");
  const [paymentURL, setPaymentURL] = useState("");
  const [loading, setLoading] = useState(false);

  const scrollToTop = () => {
    topRef.current.scrollIntoView({ behavior: "smooth" });
  };

  const handleOrder = async () => {

  
    setOpenModal(true);
    const orderid = Math.floor(Math.random() * 100000 + 1);

    const id = `AAA${orderid}`;
    //localStorage.clear();
    const orderPackage = {
      ...inputs,
      packages: order.packages,
      arrivaldate: date,
      total: order.total,
      type: "prepaid",
      uid: id,
    };
    scrollToTop()
    //   await publicRequest.post("/payment/mpesa/", {
    //  phone: `${inputs.phone}`,
    //  amount: order.total,
    //  }); */

    await publicRequest
      .post("/pesapal/payment", {
        Amount: `${order.total*130}`,
        Description: "E-book purchase",
        Type: "MERCHANT",
        Reference: "12erwe",
        PhoneNumber: `${inputs.phone}`,
      })
      .then((res) => {
        

        setLoading(true);
        setTimeout(() => {
          window.location.replace(res.data);

          setPaymentURL(res.data);
          setOpenModal(false);
          setLoading(false);

          // publicRequest.post("/orders/", orderPackage);
        }, 10000);

        console.log(res.success);
        console.log(res);

        publicRequest.post("/orders/", orderPackage);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  };

  const handleChange = (e) => {
    setInputs((prev) => {
      return { ...prev, [e.target.name]: e.target.value };
    });
  };

  const handleBankTransfer = () => {
    // setOpenBankTransfer(true);
    //setOpenModal(true)
    //setMpesa(false)
    //setPesapal(false)
  };

  const override = {
    margin: "0 auto",
    borderColor: "red",
  };
  const override1 = {
    margin: "0 auto",
    borderColor: "purple",
  };
  const override2 = {
    margin: "0 auto",
    borderColor: "blue",
  };

  const Close = () => {
    setOpenModal(false);
  };
  const handleMpesa = () => {
    //setOpenModal(true)
    setMpesa(!mpesa);
    setPesapal(false);
    //setOpenBankTransfer(false)
  };
  const handlePesapal = () => {
    setPesapal(!pesapal);
    // setOpenModal(true)
    setMpesa(false);
    // setOpenBankTransfer(false)
  };
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm();

  return (
    <div className="checkout">
      <Navbar />
      <div className="checkout-background">
        <span className="checkout-name">Checkout</span>
      </div>
      <form action="" onSubmit={handleSubmit(handleOrder)}>
        <div className="checkout-billing">
          <div className="billing">
            <div className="billing-header"></div>

            <div className="billing-details">
              <h3>Billing details</h3>
              <hr />

              <div className="billing-names">
                <div className="firstname">
                  <label htmlFor="">First Name</label>
                  <input
                    type="text"
                    name="firstname"
                    placeholder="John"
                    id="firstname"
                    {...register("firstname", { required: true })}
                    className={errors.firstname ? "invalid" : ""}
                    onChange={handleChange}
                  />

                  {errors.firstname && (
                    <span className="error-message">
                      This field is required
                    </span>
                  )}
                </div>
                <div className="lastname">
                  <label htmlFor="">Last Name</label>
                  <input
                    type="text"
                    name="lastname"
                    placeholder="Doe"
                    id="lastname"
                    {...register("lastname", { required: true })}
                    className={errors.lastname ? "invalid" : ""}
                    onChange={handleChange}
                  />
                  {errors.lastname && (
                    <span className="error-message">
                      This field is required
                    </span>
                  )}
                </div>
              </div>

              <label htmlFor="">Company Name (Optional)</label>
              <input
                type="text"
                name="company"
                placeholder="MVX"
                onChange={handleChange}
              />
              <label htmlFor="">Country of origin </label>
              <input
                type="text"
                name="country"
                placeholder="United States"
                id="country"
                {...register("country", { required: true })}
                className={errors.country ? "invalid" : ""}
                onChange={handleChange}
              />
              {errors.country && (
                <span className="error-message">This field is required</span>
              )}
              <label htmlFor="">Street Address *</label>

              <input
                type="text"
                name="streetaddress"
                placeholder="123 Toronto"
                id="streetaddress"
                {...register("streetaddress", { required: true })}
                className={errors.streetaddress ? "invalid" : ""}
                onChange={handleChange}
              />
              {errors.streetaddress && (
                <span className="error-message">This field is required</span>
              )}
              <label htmlFor="">Postcode / ZIP *</label>
              <input
                type="text"
                name="postcode"
                placeholder="25-5003"
                id="postcode"
                {...register("postcode", { required: true })}
                className={errors.postcode ? "invalid" : ""}
                onChange={handleChange}
              />
              {errors.postcode && (
                <span className="error-message">This field is required</span>
              )}
              <label htmlFor="">Town / City *</label>
              <input
                type="text"
                name="town"
                placeholder="LA"
                id="town"
                {...register("town", { required: true })}
                className={errors.town ? "invalid" : ""}
                onChange={handleChange}
              />
              {errors.town && (
                <span className="error-message">This field is required</span>
              )}
              <label htmlFor="">Whatsapp number *</label>
              <input
                type="number"
                name="phone"
                id="phone"
                {...register("phone", { required: true })}
                className={errors.phone ? "invalid" : ""}
                onChange={handleChange}
              />

              {errors.phone && (
                <span className="error-message">This field is required</span>
              )}
              <label htmlFor="">Email Address *</label>
              <input
                type="text"
                name="email"
                id="email"
                {...register("email", {
                  required: true,
                  pattern: /^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$/,
                })}
                className={errors.email ? "invalid" : ""}
                placeholder="johndoe@gmail.com"
                onChange={handleChange}
              />
              {errors.email && errors.email.type === "required" && (
                <span className="error-message">This field is required</span>
              )}
              {errors.email && errors.email.type === "pattern" && (
                <span className="error-message">Invalid email format</span>
              )}
              <input
                type="text"
                placeholder="username"
                
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="additional-information">
            <h3>Additional Information</h3>
            <hr />

            <label htmlFor="">Your Comment (Optional)</label>
            <textarea name="comment" onChange={handleChange}></textarea>

            <div className="preview-order">
              <h3>Preview Order</h3>
              <hr />
            </div>

            <div className="selected-tours">
              <div className="selected-tours-item">
                <h3>Selected Tours</h3>
                <h3>Total Price</h3>
              </div>

              <hr />
              <div>
                {order &&
                  order.packages?.map((item) => (
                    <div className="selected-tours-item">
                      <img src={item.img} alt="" />
                      <h3>
                        {item.title} ({item.duration})  x  {item.quantity}
                      </h3>
                      <h3>${item.originalPrice}</h3>
                    </div>
                  ))}
              </div>

              <hr />
              <div className="selected-tours-item">
                <h3>Subtotal</h3>
                <h3>${order && order.total}</h3>
              </div>

              <hr />
              <div className="selected-tours-item">
                <h3>Total</h3>
                <h3>${order && order.total}</h3>
              </div>

              <div className="payment-options">
                <h4>Payment options</h4>
                <div className="payment-options-item">
                  <input type="checkbox" onClick={handleMpesa} />
                  <h2>Mpesa</h2>
                  <img
                    src="data:image/png;base64,"
                    alt=""
                  />
                </div>

                {mpesa && (
                  <span>You have selected M-pesa as mode of payment</span>
                )}

                <div className="payment-options-item">
                  <input type="checkbox" onClick={handleBankTransfer} />
                  <h2>Bank Transfer</h2>
                  <img
                    src="data:image/jpeg;base64,"
                    alt=""
                  />
                </div>
                <div className="payment-options-item">
                  <input type="checkbox" onClick={handlePesapal} />
                  <h2>Pesapal</h2>
                  <img
                    src="data:image/png;base64,"
                    alt=""
                  />
                </div>
                {pesapal && (
                  <span>You have selected Pesapal as mode of payment</span>
                )}
              </div>

              <button className="pay">Place Order</button>
            </div>
          </div>
        </div>
      </form>
      {openModal && (
        <div className="bank_transfer" ref={topRef}>
          <div className="bank_transfer-item">
            <div className="close">
              <button onClick={Close}>Close</button>
            </div>
            {openBankTransfer && (
              <div className="bank_transfer-input">
                <h4>Bank Transfer information.</h4>

                <label htmlFor="">Full Name</label>
                <input type="text" />
                <label htmlFor="">Email</label>
                <input type="text" />
                <button>submit</button>
              </div>
            )}

            <div className="loading">
              <ClipLoader
                color={"#ff4838"}
                loading={loading}
                cssOverride={override}
                size={70}
                aria-label="Loading Spinner"
                data-testid="loader"
              />

              <ClipLoader
                color={"#FF7BA9"}
                loading={loading}
                cssOverride={override1}
                size={70}
                aria-label="Loading Spinner"
                data-testid="loader"
              />

              <ClipLoader
                color={"blue"}
                loading={loading}
                cssOverride={override2}
                size={70}
                aria-label="Loading Spinner"
                data-testid="loader"
              />
            </div>

            <span>Please wait. This might take a few seconds ...</span>

            <span>You are about to be redirected to payment page.</span>
          </div>
        </div>
      )}
      <Footer />
    </div>
  );
};

export default Checkout;
